import React, { useState, useEffect } from 'react';
import TinderCard from 'react-tinder-card';
import "./TinderCards.css";
import custom_axios from "./custom_axios";

function TinderCards() {

    const [people, setPeople] = useState([]);
    
    useEffect(() => {
      async function fetchData() {
        const req = await custom_axios.get('/tinder/cards');
      
        setPeople(req.data);
      }

      fetchData();
    }, [])

    console.log("The people are >>>>>>",people);

    const swiped = (direction, nameToDelete) => {
        console.log("removing: " + nameToDelete);
        // setLastDirection(direction);
    };

    const outOfFrame = (name) => {
        console.log(name + " left the screen!");
        if (name == "Vaishnavi Rudraraju") {
          alert("Peeling Sad");
        }
        else if (name == "Yashasvi Sankineni"){
          alert("Ravi Peeling Sad");
        }
        else if (name == "Paul Weasly"){
          alert("Hmm...");
        }
        else if (name == "Ravi Kiran"){
          alert("Sar Sarle Enno Anukuntam");
        }
        else {
          alert("You are just Jealous");
        }
    };

    return (
      <div className="tinderCards">
        <div className="tinderCards_cardContainer">
          {people.map((person) => (
            <TinderCard
              className="swipe"
              key={person.name}
              preventSwipe={["up", "down"]}
              onSwipe={(dir) => swiped(dir, person.name)}
              onCardLeftScreen={() => outOfFrame(person.name)}
            >
                <div 
                    style={{ backgroundImage: `url(${person.imgUrl})` }} 
                    className="card"
                >
                    <h3>{person.name}</h3>
                    <h5>{person.subtitle}</h5>
                </div>

            </TinderCard>
          ))}
        </div>
      </div>
    );
}

export default TinderCards
